}
.header .header-inner nav ul.nav-list li a:not(:only-child)::after,
.header .header-inner nav ul.nav-list li a:visited:not(:only-child)::after {
  line-height: 20px;
  font-size: 10px;
  padding-left: 4px;
  content: ' \25bc';
}
.header .header-inner nav ul.nav-list li ul.nav-dropdown {
