}
.header .header-inner nav div.nav-mobile {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  height: 64px;
  width: 64px;
  background-color: #205081;
}
.header .header-inner nav div.nav-mobile a#nav-toggle {
  position: absolute;
  left: 19px;
  top: 28px;
  cursor: pointer;
}
.header .header-inner nav div.nav-mobile a#nav-toggle span,
.header .header-inner nav div.nav-mobile a#nav-toggle span:before,
.header .header-inner nav div.nav-mobile a#nav-toggle span:after {
  cursor: pointer;
  border-radius: 1px;
  height: 4px;
  width: 25px;
  background: #fff;
  position: absolute;
  display: block;
  content: '';
  transition: all 300ms ease-in-out;
}
.header .header-inner nav div.nav-mobile a#nav-toggle span:before {
  top: -7px;
}
.header .header-inner nav div.nav-mobile a#nav-toggle span:after {
  bottom: -7px;
}
.header .header-inner nav div.nav-mobile a#nav-toggle.active span {
  background-color: transparent;
}
.header .header-inner nav div.nav-mobile a#nav-toggle.active span:before,
.header .header-inner nav div.nav-mobile a#nav-toggle.active span:after {
  top: 0;
}
.header .header-inner nav div.nav-mobile a#nav-toggle.active span:before {
  transform: rotate(45deg);
}
.header .header-inner nav div.nav-mobile a#nav-toggle.active span:after {
  transform: rotate(-45deg);
}
